@import url("https://fonts.googleapis.com/css2?family=Lobster&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&display=swap");
body {
  margin: 2px;
  font-family: 'montserrat';
  background-color: teal;
  color: orange;
}

.container {
  display: grid;
  grid-template-columns: 1fr;
}

.nav-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 30px;
}

.left-side {
  display: flex;
}

.nav-link {
  height: 22px;
  border-bottom: 1px solid transparent;
  transition: 0.5s ease-in-out;
  text-transform: uppercase;
  margin-right: 20px;
}

.nav-link a {
  color: orange;
  text-decoration: none;
}

.nav-link a:hover {
  color: black;
}

.nav-link:hover {
  border-bottom: 1px solid black;
}

.nav-link .nav-link-active {
  border-bottom: 1px solid  black;
  color: black;
  border-radius: 4px;
  transition: 0.5s ease-in-out;
}

.nav-link .nav-link-active:hover {
  border-bottom: 1px transparent;
}

@media (max-device-width: 600px) {
  .nav-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .nav-wrapper .left-side {
    display: grid;
    grid-template-columns: 1fr;
  }
}

.homepage-wrapper .btn {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.homepage-wrapper .btn button {
  width: 100%;
  background-color: orange;
  color: teal;
}

.portfolio-items-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 1px;
}

.portfolio-items-wrapper a {
  text-decoration: none;
  color: orange;
}

.portfolio-items-wrapper .item-wrapper {
  position: relative;
}

.portfolio-items-wrapper .item-img {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 350px;
  width: 100%;
}

.portfolio-items-wrapper .img-text-wrapper {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.portfolio-items-wrapper .img-text-wrapper .subtitle {
  transition: 1s ease-in-out;
  color: transparent;
  margin: 3px;
}

.portfolio-items-wrapper .img-logo-wrapper img {
  height: 85px;
  width: 93px;
  margin-bottom: 20px;
}

.portfolio-items-wrapper .img-text-wrapper:hover .subtitle {
  color: teal;
  font-weight: 400;
}

.image-blur {
  transition: 1s;
  filter: brightness(10%);
}

.para-wrapper {
  margin-right: 3px;
  font-size: 15px;
}

.para-wrapper a {
  text-decoration: none;
  color: black;
}

.para-wrapper a li {
  background-color: orange;
  width: 82px;
  border-radius: 10px;
}

.item-details-wrapper {
  display: flex;
  justify-content: center;
}

.item-details-wrapper .content-detail {
  width: 100%;
  text-align: center;
}

.item-details-wrapper .content-detail .img-wrapper img {
  width: 25vw;
  height: 50vh;
}

.lang-lib {
  display: grid;
  grid-template-columns: 1fr  1fr 1fr;
}

@media (max-device-width: 600px) {
  .homepage-wrapper .item-img {
    width: 100%;
  }
  .homepage-wrapper .btn {
    display: none;
  }
  .homepage-wrapper .portfolio-items-wrapper {
    display: grid;
    grid-template-columns: 1fr;
  }
  .homepage-wrapper .portfolio-items-wrapper .text-name {
    display: flex;
    justify-content: center;
  }
  .homepage-wrapper .lang-lib {
    display: none;
  }
  .item-details-wrapper {
    width: 100%;
  }
  .item-details-wrapper .content-detail {
    width: 100%;
  }
  .item-details-wrapper .content-detail .img-wrapper {
    width: 100%;
  }
  .item-details-wrapper .content-detail .img-wrapper img {
    width: 100%;
    height: 100%;
  }
}

.page-auth-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: calc(100vh - 84px);
}

.page-auth-wrapper .left-side-auth {
  background-size: cover;
}

.page-auth-wrapper .right-side-auth {
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-auth-wrapper .right-side-auth .form-wrapper {
  display: grid;
  grid-gap: 21px;
  background-color: transparent;
}

.page-auth-wrapper .right-side-auth .form-wrapper button {
  background-color: teal;
  color: white;
  font-weight: bold;
  outline: none;
  text-decoration: none;
  border: 0px;
  padding: 5px;
  transition: 0.5s ease-in-out;
}

.page-auth-wrapper .right-side-auth .form-wrapper button:focus {
  outline: none;
}

.page-auth-wrapper .right-side-auth .form-wrapper button:hover {
  background-color: lightblue;
  cursor: pointer;
  color: orange;
}

.page-auth-wrapper .right-side-auth .form-wrapper button {
  background-color: orange;
}

.page-auth-wrapper .right-side-auth .form-wrapper input {
  font-family: 'Times New Roman', Times, serif;
  color: teal;
  width: 100%;
  padding: 5px 0px;
  border-top: 0px;
  border-right: 0px;
  border-left: 0px;
  border-bottom: 1px solid black;
  transition: 0.5s all ease-in-out;
  font-size: 1.1em;
  margin-bottom: 21px;
  background-color: transparent;
}

.page-auth-wrapper .right-side-auth .form-wrapper input:focus {
  outline: none;
  border-bottom: 1px solid darkgoldenrod;
}

.page-auth-wrapper .right-side-auth .form-wrapper .form-group {
  display: flex;
}

.page-auth-wrapper .right-side-auth .form-wrapper .form-group svg {
  font-size: 2em;
  margin-right: 15px;
}

