.about-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  height: calc(100vh - 83px);
}

.about-container .img-container img {
  width: 100%;
}

.about-container .text-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-container .text-container .text-wrapper {
  width: 75%;
  display: flex;
  justify-content: center;
}

body {
  margin: 0px;
}

@media (max-width: 600px) {
  .about-container {
    display: flex;
  }
  .about-container .img-container {
    display: none;
  }
  .about-container .text-container {
    border: 1px solid orange;
    border-radius: 20px;
  }
}

.contact-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: calc(100vh - 83px);
}

.contact-container .left-column img {
  width: 100%;
}

.contact-container .right-column {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-device-width: 600px) {
  .contact-container {
    font-size: 12px;
  }
}

.git-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}

.git-container .git-wrapper {
  text-align: center;
  background-color: transparent;
  border-radius: 20px;
  transition: 0.5s ease-in-out;
  font-family: 'Nanum Myeongjo', serif;
  font-size: 13px;
  border: 1px solid #06305c;
}

.git-container .git-wrapper .clone-url {
  cursor: pointer;
}

.git-container .git-wrapper a {
  text-decoration: none;
  color: orange;
}

.git-container .git-wrapper:hover {
  color: teal;
  background-color: orange;
  box-shadow: 1px 1px 10px black;
  border-radius: 1px;
}

.git-container .git-wrapper:hover a {
  color: teal;
}

.main-git {
  display: flex;
  justify-content: center;
  font-family: 'Lobster', cursive;
  text-decoration: underline;
}

@media (max-device-width: 600px) {
  .git-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .git-container .git-wrapper {
    font-size: 8px;
  }
}
